body {
  background-color: #ffffff;
  padding: 0;
  margin: 0;
  font-family: "Lato", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  color: #444444;
}
a {
  text-decoration: none;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/oswald/oswaldregular.woff2") format("woff2");
  src: url("../fonts/oswald/oswaldregular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 500;
  src: url("../fonts/oswald/oswaldregular.woff2") format("woff2");
  src: url("../fonts/oswald/oswaldregular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Lato";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/lato/Lato-Regular.woff2") format("woff2");
  src: url("../fonts/lato/Lato-Regular.woff") format("woff");
  font-display: swap;
}
.page {
  height: 100%;
}
.page__body {
  min-height: 100%;
  margin: 0;
  padding: 0;
}
@media (min-width: 1440px) {
  .page__body {
    position: relative;
  }
  .page__body::before {
    content: "";
    width: 50%;
    background-color: #64a73c;
    height: 694px;
    position: absolute;
    top: 0;
    right: 0;
    z-index: -1;
  }
}
.page__body--forum {
  min-height: 100%;
  margin: 0;
  padding: 0;
  background-color: transparent;
}
.main {
  position: relative;
  padding: 42px 11px 0;
  background: url("../img/textlogo.svg");
  background-repeat: no-repeat;
  background-position: center 22px;
}
