.toggle {
  border: none;
  position: relative;
  padding: 0;
  cursor: pointer;
  height: 24px;
  width: 24px;
  background-color: transparent;
  background-repeat: no-repeat;
}
.toggle--opened {
  background-image: url("../img/main-nav-open-button-icon.svg");
}
.toggle--closed {
  background-image: url("../img/close-button-icon.svg");
}
.toggle--no-js {
  display: none;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
