.hero__wrapper {
  position: relative;
  padding-bottom: 151px;
  padding-top: 12px;
}
.hero__wrapper::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  display: block;
  background-image: url("../img/cat-background.jpg");
  background-repeat: no-repeat;
  background-size: auto 100%;
  background-position: center center;
  background-color: #63a63c;
  width: 100%;
  height: 100%;
}
@media (min-width: 768px) {
