    margin: 0 auto;
  }
}
.extra__legend {
  margin: 0;
  padding: 0;
}
@media (min-width: 768px) {
  .extra__legend {
    padding: 0 33px;
  }
}
@media (min-width: 1440px) {
  .extra__legend {
    padding: 0 71px;
  }
}
.extra__title {
  padding: 1px 22px 33px 19px;
}
@media (min-width: 768px) {
  .extra__title {
