.extra__item {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  justify-content: space-between;
  margin: 0;
  border-top: 1px solid #cdcdcd;
  gap: 6px;
  border-bottom: 1px solid #cdcdcd;
  padding: 0 0 17px;
}
@media (min-width: 768px) {
  .extra__item {
    flex-direction: row;
    padding: 14px 39px 28px;
    justify-content: space-between;
    gap: 5px 195px;
    border-top: unset;
  }
  .extra__item:last-child {
    border-bottom: unset;
  }
}
@media (min-width: 1440px) {
  .extra__item {
    gap: 0 29px;
    padding: 13px 0 4px 39px;
  }
}
@media (min-width: 1440px) {
  .extra__item:last-child {
    border-bottom: 1px solid #cdcdcd;
  }
}
.extra__item-wrapper {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
}
@media (min-width: 768px) {
  .extra__item-wrapper {
    align-self: center;
    gap: 0 23px;
    justify-content: flex-end;
    margin: 0;
  }
}
@media (min-width: 1440px) {
  .extra__item-wrapper {
    justify-content: flex-start;
  }
}
@media (min-width: 768px) {
  .extra__item-wrapper {
    align-self: center;
    gap: 0 52px;
  }
}
@media (min-width: 1440px) {
  .extra__item-wrapper {
    align-self: center;
    gap: 0 96px;
  }
}
.extra__item-title {
  display: block;
  position: relative;
  font-family: "Oswald", "Arial", sans-serif;
  text-transform: uppercase;
  font-size: 16px;
  font-weight: 400;
  line-height: 20px;
  margin: 0;
  padding: 12px 0 0;
}
@media (min-width: 768px) {
  .extra__item-title {
